import React, { useState } from 'react'
import { useDispatch } from 'react-redux'
import * as Yup from 'yup'
import { useFormik } from 'formik'
import { useHistory } from 'react-router-dom'
import Button from 'components/Button'
import { Fonts } from '../../../shared/constants/AppEnums'
import InputField from 'components/InputField'
import { loginRequest } from '../../../redux/auth/actions'
import { InputAdornment, IconButton } from '@mui/material'
import { Icon } from '@iconify/react'
import AccountCircle from '@mui/icons-material/AccountCircle'
import EnhancedEncryptionSharpIcon from '@mui/icons-material/EnhancedEncryptionSharp'
import './index.scss'
import { AiFillDropboxCircle } from 'react-icons/ai'
const LoginPage = () => {
	const history = useHistory()
	const dispatch = useDispatch()
	const [showPassword, setShowPassword] = useState('password')
	const changePasswordState = () => {
		if (showPassword === 'password') {
			setShowPassword('text')
		} else {
			setShowPassword('password')
		}
	}

	const LoginSchema = Yup.object().shape({
		email: Yup.string().required('Email is required'),
		password: Yup.string().required('Password is required'),
	})

	const loginFormik = useFormik({
		initialValues: {
			email: '',
			password: '',
			remember: true,
		},
		validationSchema: LoginSchema,
		onSubmit: async (values) => {
			await dispatch(
				loginRequest({
					email: values.email,
					password: values.password,
				})
			)
		},
	})

	return (
		<>
			<div className='formOne_main'>
				<div className='formOne_heading'>
					<span className='formOne_heading_text'>
						<AiFillDropboxCircle />
						Arkad
					</span>
				</div>
				<div className='formOne_body_main col-12 '>
					<div className='formOne_body col-8 offset-2'>
						<p className='formOne_body_heading'>Welcome</p>
						<p>Please login to your account</p>
						<form
							className='form'
							noValidate
							autoComplete='off'
							onSubmit={loginFormik.handleSubmit}
						>
							<div className='mb-3 col-8 offset-2  '>
								<div className='formOne_main_inp1'>
									<InputField
										className='formInput'
										name='email'
										type='email'
										placeholder='name@email.com'
										value={loginFormik.values.email}
										onChange={(e: any) => {
											loginFormik.setFieldValue('email', e.target.value)
										}}
									/>
									<AccountCircle className='iconUser' />
									{loginFormik.touched.email && loginFormik.errors.email ? (
										<div style={{ color: 'red' }}>
											{loginFormik.errors.email}
										</div>
									) : null}
								</div>
							</div>
							<div className='mb-3 col-8 offset-2'>
								<div className='formOne_main_inp2'>
									<InputField
									type="text"
										className='formInput'
										name='password'
										// type={showPassword}
										placeholder='password'
										value={loginFormik.values.password}
										onChange={(e: any) => {
											loginFormik.setFieldValue('password', e.target.value)
										}}
									/>
									<EnhancedEncryptionSharpIcon className='iconPass' />
									{loginFormik.touched.password &&
									loginFormik.errors.password ? (
										<div style={{ color: 'red' }}>
											{loginFormik.errors.password}
										</div>
									) : null}
								</div>
							</div>

							<a href='#' className='forgot col-8 offset-2'>
								Forgot Password?
							</a>
							<Button type='submit' className='formOne_btn col-8 offset-2 '>
								LOGIN
							</Button>
						</form>
						<p className='adduser'>
							Don't have an account ?{' '}
							<a href='#' className='color'>
								ADD USER
							</a>
						</p>
					</div>
				</div>
			</div>
		</>
	)
}

export default LoginPage
