import React, { FC } from 'react'
import styles from './style.module.scss'

interface FuncProp {
	className?: string
	placeholder?: string
	error?: boolean
	errorClass?: string
	helpertext?: string
	value?: any
	onChange?: any
	onKeyDown?: any
	onKeyUp?: any
	name?: any
	type?: string
	min?: any
	max?: any
	maxLength?: any
	onKeyPress?: any
	style?: any
	pattern?: any
	onfocus?: any
	onblur?: any
	ref?: any
}
const InputField: FC<FuncProp> = ({
	placeholder,
	type,
	className,
	error,
	errorClass,
	helpertext,
	onChange,
	onKeyDown,
	onKeyUp,
	value,
	name,
	min,
	style,
	max,
	maxLength,
	pattern,
	onKeyPress,
	onfocus,
	onblur,
	ref,
}) => {
	return (
		<div className={`wrapDivMargin ${styles.inputDesign}`}>
			<input
				type={type}
				placeholder={placeholder}
				className={`form-control ${styles.inputStyle} ${className}`}
				name={name}
				value={value}
				onChange={onChange}
				onKeyDown={onKeyDown}
				onKeyUp={onKeyUp}
				min={min}
				max={max}
				style={style}
				pattern={pattern}
				maxLength={maxLength}
				onKeyPress={onKeyPress}
				onFocus={onfocus}
				onBlur={onblur}
				ref={ref}
			/>
		
		</div>
	)
}

export default InputField
